import uuidv4 from "uuid/v4";

let tasks = [];

const loadTasks = () => {
  const tasksJSON = localStorage.getItem("tasks");

  try {
    tasks = tasksJSON ? JSON.parse(tasksJSON) : [];
  } catch (e) {
    return [];
  }
};

const saveTasks = () => {
  localStorage.setItem("tasks", JSON.stringify(tasks));
};

const getTasks = () => tasks;

const createTask = () => {
  const id = uuidv4();

  tasks.push({
    id: id,
    taskName: "",
    arrivalTime: "",
    executionTime: "",
    endingDeadline: "",
    priodic: false,
  });
  saveTasks();

  return id;
};

loadTasks();
export { createTask, loadTasks, saveTasks, getTasks };
